.theme__name {
  font-size: 24px;
  line-height: 29px;
  font-weight: 700;
  margin-top: 0;
  margin-bottom: 8px;
  color: $white;
}

.theme__hit {
  width: 67px;
  padding: 3px 17px 4px 16px;
  border-radius: 25px;
  border: 2px solid rgba(242, 137, 15, 0.74);
  position: absolute;
  top: 17px;
  right: 23px;
  display: flex;
  align-items: center;
}

.theme__hit span {
  font-size: 12px;
  line-height: 17px;
  font-weight: 500;
  color: $gray;
}


.theme__address,
.theme__guests-ammount,
.theme__level {
  font-size: 13px;
  line-height: 19px;
  font-weight: 500;
  color: $gray;
  position: relative;
}

.theme__address {
  font-style: normal;
  display: flex;
  align-items: center;
  margin-bottom: 7px;
}

.theme__address span {
  margin-left: 5px;
}

.theme__guests-ammount span {
  margin-left: 10px;
}

.theme__level span {
  margin-left: 9px;
}

.theme__guests-ammount {
  padding: 0 10px 0 0;
  border-right: 1px solid rgba(255, 255, 255, 0.5);
  display: flex;
  align-items: center;
}

.theme__level {
  padding-left: 13px;
  display: flex;
  align-items: center;
}

.theme {
  width: 344px;
  border-radius: 3px;
  background-repeat: no-repeat;
  background-size: 100% 232px;
  position: relative;
  margin-bottom: 32px;
  justify-content: space-between;
}

.theme:hover {
  box-shadow: 6px 0 5px 10px rgba(0, 0, 0, 0.5), 0 6px 5px 10px rgba(0, 0, 0, 0.5);
}

.theme:active {
  opacity: 0.7;
}

.theme__wrapper {
  padding: 132px 90px 20px 15px;
  background-image: linear-gradient(0.17deg, rgba(28, 27, 27, 0.9) 5.23%, rgba(46, 46, 46, 0) 98.38%);
  background-size: 100% 160px;
  background-position: 0 72px;
  background-repeat: no-repeat;
}

.theme:nth-child(3n-1) {
  margin: 0 24px 32px;
}

.theme:first-child {
  background-image: url("../img/backgrounds/bg-theme-1@1x.jpg");

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("..//img/backgrounds/bg-theme-1@2x.jpg");
    background-size: 100% auto;
  }
}

