.theme:nth-child(5) .theme__wrapper,
.theme:nth-child(7) .theme__wrapper {
  padding-top: 104px;
}

.theme__details {
  display: flex;
}

@media (max-width: 767px) {
  .theme:nth-child(3n-1) {
    margin: 0 auto;
    margin-bottom: 16px;
  }

  .theme {
    width: 328px;
    margin: 0 auto;
    margin-bottom: 16px;
    flex-grow: -1;
  }

  .theme__hit-wrapper {
    right: 16px;
  }

  .theme__hit {
    padding-top: 3px;
    padding-bottom: 3px;
  }

  .theme__hit::before {
    top: 3px;
  }
}
